@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/ptmono.woff2") format("woff2"), url("../fonts/ptmono.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/robotocondensed.woff2") format("woff2"), url("../fonts/robotocondensed.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/robotocondensedbold.woff2") format("woff2"), url("../fonts/robotocondensedbold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #000000;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  padding-right: 0;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

ul {
  list-style-type: none;
  margin: 0;
  padding-left: 0;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  margin: 0 auto;
}
@media (max-width: 1023px) {
  .container {
    max-width: 1023px;
  }
}
@media (max-width: 767px) {
  .container {
    max-width: 767px;
  }
}

.button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  cursor: pointer;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 0.05em;
}
.button:hover {
  opacity: 0.7;
}
.button:focus {
  opacity: 0.7;
  outline: none;
}
.button:active {
  opacity: 0.4;
  outline: none;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: start;
  width: 100%;
  min-height: 100%;
  padding: 145px 40px 0 25px;
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 500px;
  padding: 40px 40px 44px;
  background: #00b2ff;
  border-radius: 3px;
}
.modal__close-btn {
  position: absolute;
  top: 17px;
  right: 17px;
  z-index: 1;
  fill: #ffffff;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}
.modal__button {
  width: 100%;
  background: #041827;
  border-radius: 3px;
  padding: 15px 25px 11px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  font-weight: 700;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #ffffff;
  border: none;
  margin-right: 50px;
}

.modal__form-wrapper {
  position: relative;
}
.modal__form-wrapper .input-message {
  position: absolute;
  left: 0;
  top: 35px;
  font-family: "PT Mono", "Arial", sans-serif;
  padding-top: 0;
  font-size: 14px;
  color: #ff0000;
}

.modal__form-wrapper input {
  display: none;
}

.modal__form-wrapper span {
  display: block;
  padding-left: 33px;
  padding-top: 20px;
  opacity: 0.8;
  font-size: 14px;
  color: #ffffff;
  text-align: justify;
}

.modal__form-wrapper label {
  position: relative;
}

.modal__form-wrapper label::before {
  position: absolute;
  content: "";
  width: 15px;
  height: 15px;
  border: 1px solid #ffffff;
  border-radius: 2px;
  top: 20px;
  left: 8px;
}

.modal__form-wrapper svg {
  display: none;
  position: absolute;
  top: 24px;
  left: 10px;
}

.modal__form-wrapper input:checked ~ svg {
  display: block;
}

.modal-form {
  display: flex;
  flex-direction: column;
  color: #ffffff;
  justify-content: space-between;
}
.modal-form .input-message {
  position: absolute;
  left: 0;
  top: 50px;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 14px;
  color: #ff0000;
}

.modal-form h2 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 30px;
  line-height: 42px;
  margin: 0;
}

.modal-form p {
  line-height: 22px;
  margin: 0 0 38px;
  width: 100%;
  padding-right: 20px;
}

.modal-form__wrapper {
  flex-grow: 1;
  position: relative;
}

.modal-form input {
  line-height: 22px;
  width: 100%;
  background: rgba(4, 20, 39, 0.15);
  border-radius: 3px;
  padding: 13px 15px 15px;
  border: none;
  margin-bottom: 20px;
  color: #ffffff;
}
.modal-form input::placeholder {
  color: #ffffff;
}

.modal-form textarea {
  width: 100%;
  min-height: 155px;
  padding: 25px 15px;
  background: rgba(4, 20, 39, 0.15);
  border-radius: 3px;
  border: none;
  color: #ffffff;
  margin-bottom: 26px;
}
.modal-form textarea::placeholder {
  color: #ffffff;
}

