[data-accordion=element].is-active > [data-accordion=content] {
  max-height: 100%;
}

[data-accordion=parent].no-transition [data-accordion=content] {
  transition: none;
}

[data-accordion=content] {
  max-height: 0;
  transition: max-height 0.3s;
  overflow: hidden;
}

.accordion__element {
  position: relative;
  cursor: pointer;
}

@media (max-width: 767px) {
  .accordion__element::before {
    content: "";
    position: absolute;
    width: 15px;
    height: 1px;
    background: #c4c4c4;
    right: 26px;
    top: 28px;
  }
}

@media (max-width: 767px) {
  .accordion__element::after {
    content: "";
    position: absolute;
    width: 15px;
    height: 1px;
    background: #c4c4c4;
    right: 26px;
    top: 26px;
    transform: rotate(90deg);
  }
}

.is-active .about__button-close {
  display: block;
}

.is-active .about__button-open {
  display: none;
}

@media (max-width: 767px) {
  .is-active.accordion__element::after {
    display: none;
  }
}

@media (max-width: 767px) {
  .is-active.main-footer__nav {
    padding-bottom: 16px;
  }
}

@media (max-width: 767px) {
  .is-active.main-footer__contacts {
    padding-bottom: 16px;
  }
}

